html, body{
  height: 100%;
}
body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  height: 100%;
  a{
    color: #EE7700!important;
  }
  .bg-primary{
    background-color: #EE7700!important;
  }
 
  .main .left{
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    background: url('/images/Waves.png') no-repeat;
    background-size: cover;
    

    h2{
      font-size: 42px;
      line-height: 42px;

      strong{
        font-weight: 900;
      }
    }
    h1{
      font-weight: 900;
      font-size: 54px;
      line-height: 56px;
    }

    small{
      line-height: 1.2;
      font-size: 12px;
    }

  }

  .main .right{
    background-color: #F4F4F4;

    .back-link{
      color: #010101!important;
      text-transform: uppercase;
      text-decoration: none;
      letter-spacing: 1px;
      font-size: 12px;
      font-weight: 700;
    }
    .btn{
      min-width: 140px;
      text-align: center;
    }
  }

  

}

.modal-backdrop {
  background-color:rgba(0, 43, 54, 1);
  //background-color: red!important;
}
.modal-backdrop.show {
  opacity: 1;
}

.modal-content{
  border-radius: 20px;
}

.btn-primary{
  background-color: #EE7700!important;
  border-color: #EE7700!important;
  font-weight: 600;
  letter-spacing: 2px;
}
.btn-outline-primary{
  border-color: #EE7700!important;
  color: #EE7700!important;
  font-weight: 600;
  letter-spacing: 2px;
  border-width: 2px;

  &:hover{
    background-color: #EE7700!important;
    border-color: #EE7700!important;
    color: #fff!important;
  }
}

